<template>
  <div class="main about-service">
    <Header
      :transparent.sync="transparent"
      :mobile-icon-active.sync="mobileIconActive"
    />
    <StartScreen>
      <span slot="subtitle">
        Поиск автомобиля
        <br />
        для вашего груза
      </span>
      <nuxt-link
        slot="link"
        :to="'/how-it-works/shipper/1/'"
        class="md-raised outline link-button"
      >
        Как это работает?
      </nuxt-link>
      <div slot="video" class="video_wrapper">
        <video preload="auto" loop muted autoplay class="video">
          <source src="~/assets/video/main/1.mp4" type="video/mp4" />
        </video>
      </div>
      <img
        slot="poster"
        src="/img/poster2.jpg"
        class="poster"
        alt="Сервис MoreCargo для грузовладельцев"
      />
    </StartScreen>
    <Owners :list="list" type="shipper" />
    <PromoBlock promo-title="сервис грузоперевозок по России" />
    <Steps :steps="steps" />
    <FAQ :faq-list="faqList" gray />
    <Questions type="shipper" />
    <Footer />
  </div>
</template>

<script>
import Header from '@/components/Header';
import StartScreen from '@/components/start-screen/StartScreen';
import Owners from '@/components/Owners';
import PromoBlock from '@/components/PromoBlock';
import Steps from '@/components/Steps';
import FAQ from '@/components/FAQ';
import Questions from '@/components/Questions';
import Footer from '@/components/Footer';

import { shipperItems as list } from '@/assets/enums/slides';
import { shipperSteps as steps } from '@/assets/enums/steps';

import axios from 'common/helpers/httpService';

export default {
  head: {
    title:
      'Перевозки грузов по России - найти транспорт и перевозчика для доставки груза',
    meta: [
      { name: 'url', content: 'https://morecargo.ru/' },
      {
        name: 'description',
        content:
          'Грузоперевозки по России. Онлайн-сервис для поиска транспорта и перевозчика.'
      }
    ]
  },
  components: {
    Header,
    StartScreen,
    Owners,
    PromoBlock,
    Steps,
    FAQ,
    Questions,
    Footer
  },
  data() {
    return {
      transparent: true,
      scrolled: 0,
      mobileIconActive: false
    };
  },
  async asyncData() {
    const data = {
      faqList: []
    };

    try {
      const response = await axios.get(
        '/faq/get?category=shipper&title=UL&type=users'
      );
      data.faqList = response.content || [];
    } catch (error) {
      console.error(error);
    }

    return data;
  },
  created() {
    this.steps = steps;
    this.list = list;
  },
  mounted() {
    this.fixedHeader();
    document.addEventListener('scroll', this.fixedHeader);
  },
  methods: {
    fixedHeader(num) {
      const scrolled = window.pageYOffset || document.documentElement.scrollTop;
      if (scrolled > 0 || this.mobileIconActive === true) {
        this.transparent = false;
      } else {
        this.transparent = true;
      }
    }
  }
};
</script>

